import { newSpecPage } from '@stencil/core/testing';
import { PrestoPlayerButton } from '../presto-player-button';

describe('presto-player-button', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [PrestoPlayerButton],
      html: `<presto-player-button></presto-player-button>`,
    });
    expect(page.root).toEqualHtml(`
      <presto-player-button size="medium" type="default">
        <mock:shadow-root>
          <button class="button button--default button--has-label button--medium" part="base" type="button">
            <span class="button__prefix" part="prefix">
              <slot name="prefix"></slot>
            </span>
            <span class="button__label" part="label">
              <slot></slot>
            </span>
            <span class="button__suffix" part="suffix">
              <slot name="suffix"></slot>
            </span>
          </button>
        </mock:shadow-root>
      </presto-player-button>
    `);
  });
});
